




































































































import { Component, Prop, Vue, Ref } from "vue-property-decorator";
import Chart from "chart.js";
import {
  listProjects,
  Project,
  Device,
  listDevices,
  fetchEvents
} from "@/modules/DTAPI";
const rust = import("../../../dtgaussprocess/pkg/dtgaussprocess");

interface DataPoint {
  timestamp: number;
  value: number;
}

@Component
export default class HelloWorld extends Vue {
  @Prop() private msg!: string;
  @Ref("canvas") canvas!: HTMLCanvasElement;

  ynoise = 1e-3;
  lengthScale = 1;
  amplitude = 1;
  username = "";
  password = "";

  projects: Project[] = [];

  devices: Device[] = [];

  selectedProject: Project | null = null;
  selectedDevice: Device | null = null;

  sampleX: number[] = [];
  y: number[] = [];
  noiseY = 0.05;

  mounted() {
    if (!this.canvas) {
      console.error("Canvas not found");
      return;
    }
    this.canvas.width =
      this.canvas.parentElement?.clientWidth || this.canvas.width;
    this.canvas.height =
      this.canvas.parentElement?.clientHeight || this.canvas.height;
  }

  onUserInfo() {
    listProjects(this.username, this.password).then(p => {
      this.projects = p;
      if (this.projects.length > 0) {
        this.selectedProject = this.projects[0];
        this.onProjectSelection();
      }
    });
  }

  onProjectSelection() {
    if (this.selectedProject) {
      listDevices(this.username, this.password, this.selectedProject.name).then(
        d => {
          this.devices = d;
        }
      );
    }
  }

  onDeviceSelection() {
    if (this.selectedDevice) {
      fetchEvents(this.username, this.password, this.selectedDevice.name, [
        "temperature"
      ])
        .then(events =>
          events.map(e => {
            if (!e.data.temperature) {
              return null;
            }
            return {
              timestamp:
                new Date(e.data.temperature.updateTime).getTime() / 1000,
              value: e.data.temperature.value
            };
          })
        )
        .then(events => {
          const filtered = events.filter(e => e) as DataPoint[];
          this.y = filtered.map(e => e.value);
          this.doGP();
        });
    }
  }

  doGP() {
    rust.then(rust => {
      const N = this.y.length;
      const idx = [...Array(N).keys()];
      const x = idx.map(i => -1 * i).reverse();
      const sampleX = x
        .slice(1)
        .flatMap(x => [...Array(10).keys()].reverse().map(i => x - i / 10));
      const gp = rust.GaussianProcess.new(
        Float64Array.from(x),
        Float64Array.from(this.y)
      );
      const post = gp.get_posterior(
        sampleX,
        this.lengthScale,
        this.amplitude,
        this.noiseY
      );
      const createPoint = (y: number, i: number) => {
        return { y, x: sampleX[i] };
      };
      const mean = Array.from(post.mean()).map(createPoint);
      const ciLow = Array.from(post.ci_low()).map(createPoint);
      const ciHigh = Array.from(post.ci_high()).map(createPoint);

      const measurements = this.y.map((y, i) => {
        return { x: x[i], y };
      });

      const ctx = this.canvas.getContext("2d");
      if (ctx) {
        const chart = new Chart(ctx, {
          type: "line",
          data: {
            datasets: [
              {
                data: mean,
                label: "Posterior Mean",
                fill: false,
                borderColor: "#F0522C",
                pointRadius: 0
              },
              {
                data: ciLow,
                fill: false,
                borderColor: "rgba(0, 0, 255, 0.5)",
                pointRadius: 0
              },
              {
                data: ciHigh,
                fill: "-1",
                borderColor: "rgba(0, 0, 255, 0.5)",
                backgroundColor: "rgba(0, 0, 255, 0.1)",
                pointRadius: 0
              },
              {
                data: measurements,
                pointBackgroundColor: "#000000",
                pointBorderColor: "#000000",
                showLine: false,
                fill: false
              }
            ]
          },
          options: {
            scales: {
              xAxes: [
                {
                  type: "linear"
                }
              ]
            }
          }
        });
      }
    });
  }
}
